import React from "react";

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import { useNavigate } from "react-router-dom";


function Signup() {

  let emailRef = React.useRef('');
  let passwordRef = React.useRef('');
  let confirmPasswordRef = React.useRef('');
  let navigate = useNavigate();
  let [result, setResult] = React.useState({});

  const handleSignupSubmit = (evt) => {
    evt.preventDefault();
    const signupObject = {
      email: emailRef.current.value,
      password: passwordRef.current.value,
      confirmPassword: confirmPasswordRef.current.value
    };
    try {
      import("../../model/db").then(async (db) => {
        const res = await db.create([signupObject], "users");
        setResult(res);
      });
    } catch (error) {
      console.warn(error);
    }

  }

  return (
    <Container className="w-50">
      <Form onSubmit={(ev) => handleSignupSubmit(ev)}>
        <Form.Group className="mb-3" controlId="email">
          <Form.Label>Email address</Form.Label>
          <Form.Control type="email" placeholder="Enter email" ref={emailRef} />
          <Form.Text className="text-muted">
            We'll never share your email with anyone else.
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control type="password" placeholder="Password" ref={passwordRef} />
        </Form.Group>

        <Form.Group className="mb-3" controlId="confirmPassword">
          <Form.Label>Confirm password</Form.Label>
          <Form.Control type="password" placeholder="Confirm password" ref={confirmPasswordRef} />
        </Form.Group>
        <div className="text-center">
          <span className="text-success d-block">{result.result}</span>
          <Button variant="success" type="submit" className="btn-sm">
            Sign up
          </Button> &nbsp;&nbsp;
          {
            result.result? (<Button variant="info" onClick={() => navigate("/recruiter/auth/signin", { replace: true })} className="btn-sm">
              Log in
            </Button>):""
          }
        </div>
      </Form>
    </Container>
  );
}

export default Signup;